.login-sub-container{
    /* border: 2px solid red; */
    display: flex;
    width: 40%;
    height: 350px;
    
    position: absolute;
    top: 20%;
    left: 30%;
    box-shadow: 0px 0px 10px 0px rgb(255, 255, 255);
}
.imageField{
    width: 50%;
    /* border: 2px solid rgb(36, 3, 3); */
    background-image: url('data:image/jpeg;base64,');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}
.login-form{
    border: 2px solid green;
    width: 50%;
    font-size: 30px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    color: rgb(75, 75, 255);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding-left: 20px;
    text-align: center;
    animation: fontColorChange 10s infinite;
   
}
@keyframes fontColorChange {
    0% {
      color: rgb(25, 0, 255);
    }
    20% {
      color: rgb(255, 75, 165);
    }
    30% {
      color: rgb(255, 255, 255);
    }
    
      40% {
        color: rgb(77, 255, 68);
      }
      50% {
        color: rgb(19, 224, 255);
      }
      60% {
        color: rgb(129, 15, 15);
      }
      70% {
        color: rgb(255, 148, 66);
      }
      80% {
        color: rgb(247, 7, 255);
      }
      90% {
        color: rgb(245, 255, 111);
      }
      100% {
        color: rgb(255, 255, 255);
      }
  }
.login-form input{
    height: 40px;
    width: 70%;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding-left: 20px;
    
}
.login-form button{
    height: 40px;
    width: 50%;
    border-radius: 20px;
    background-color: red;
    color: white;
    font-size: 20px;
    font-weight: 800;
}
.loginVideo{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.login-main-container{
    /* border: 2px solid red; */
    position: absolute;
    height: 100%;
    width: 100%;
   
}
.imageField{
    text-align: center;
    padding-top: 20%;
    color: white;
    font-weight: 900;
    opacity: 0.8;
}
.loginDiv2{
    position: absolute;
    top: -20px;
    font-size: 15px;
    color: black;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 900;
    opacity: 0.6;
}
#passwordVisible{
    position: absolute;
    left:89%;
    top: 55.8%;
    width:20px;
    
}