.homeContainer{
    /* border: 2px solid green; */
    height: 590px;
    position: absolute;
    top: 90px;
    width: 99.7%;
}
.homeSubCointainer{
    /* border: 2px solid red; */
    width: 70%;
    height: 80px;
    display: flex;
    background-color: white;
    border-radius: 50px;
    position: absolute;
    top: 40%;
    left: 15%;
    box-shadow: 0px 0px 10px 0px rgb(20, 1, 1);
}
.Katha{
    width: 50%;
    /* border: 2px solid white; */
    border-radius: 50px;
   
}
.Katha button{
    height: 80px;
    border-radius: 50px;
    width: 100%;
    border: 0px;
    font-size: 25px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 900;
    color: rgb(255, 167, 15);
    cursor: pointer;
}
.Katha button:hover{
    background-color: orange;
    color: white;
}
.payment{
    width: 50%;
}
.payment button{
    height: 80px;
    border-radius: 50px;
    width: 100%;
    border: none;
    font-size: 25px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 900;
    color: green;
    cursor: pointer;
   
}
.payment button:hover{
    background-color: green;
    color: white;
}
video{
    width: 100%;
    height: 100%;
    object-fit: cover; 
}
.link{
    color: rgb(255, 255, 255);
    font-size: 23px;
    background-color: red;
    border-radius: 20px;
    
}
.heading1{
    position: absolute;
    bottom: 70%;
    right: 40%;
    font-size: 100px;
    opacity: 0.1;
    
    border-radius: 100px;
}
.heading2{
    position: absolute;
    top: 70%;
    right: 15%;
    font-size: 100px;
    opacity: 0.1;
    
    border-radius: 100px;
}