.mainContainer{
    /* border: 2px solid red; */
    height: 570px;
    position: absolute;
    width: 100%;
    bottom: 65px;
    color:white;
   
}
.subContainer{
    display: flex;
    /* border: 2px solid white; */
    height: 550px;
    margin-top: 10px;
    border-radius: 20px;
    width: 100%;
    background-color: rgb(0, 2, 3);
}
.tableContainer{
    height: 500px;
    width: 70%;
    /* border: 2px solid red; */
    margin-top: 20px;
    margin-left: 10px;
    border-radius: 20px;
    background-color: rgb(255, 183, 14);
    box-shadow: 0px 0px 5px 0px white;
}
.tableData{
    width: 80%;
    /* border: 2px solid orange; */
    border-radius: 20px;
    margin-left: 10%;
    margin-top: 20px;
    scroll-behavior: auto;
    text-align: center;
    font-size: 20px;
    font-weight: 900;
    background-color: rgb(255, 255, 255);
    color: black;
    border-radius: 20px;
    

}
.formContainer{
    /* border: 2px solid rgb(9, 243, 9); */
    height: 500px;
    margin-top: 20px;
    border-radius: 20px;
    width: 25%;
    margin-left: 30px;
    background-color: rgb(15, 7, 51);
    box-shadow: 0px 0px 10px 0px white;
    
}
.formdata{
    /* border: 2px solid rgb(194, 82, 82); */
    height: 450px;
    margin-top: 20px;
    width: 90%;
    margin-left: 20px;
    border-radius: 20px;
    background-color: rgb(25, 228, 79);
    box-shadow: 0px 0px 10px 0px white;
    display: flex;
    flex-direction: column;
   

}
.formdata div{
    padding-left: 90px;
    height: 100px;
    padding-top: 20px;
    font-size: 20px;
    font-weight: 600;
    color: rgb(53, 53, 240);
}
.formdata1 div input{
    height: 40px;
    width: 200px;
    border-radius: 20px;
    font-size: 20px;
    font-weight: 800;
    color: red;
    padding-left: 20px;
    
}
.formdata1 div button{
    height: 40px;
    width: 150px;
    border-radius: 20px;
    background-color: blue;
    color: white;
    font-size: 20px;
    font-weight: 900;
    margin-left: 30px;
    margin-top: 20px;
    padding-bottom: 10px;
    
}
span{
   position: absolute;
   top: 1px;
   font-size: 20px;
   font-weight: 800;
   left: 20px;
   letter-spacing: 2px;
   word-spacing: 5px;
   color: rgb(77, 254, 22);
}
.formdata1{
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 20px;
    color: white;
    font-weight: 800;
}
.searchKatha{
    position: absolute;
    left: 40%;
    width: 20%;
    height: 40px;
    top: -10px;
    border-radius: 20px;
    font-size: 20px;
    padding-left: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;

}
.message{
    position: absolute;
    top: -2%;
    left: 40%;
    border: 2px solid white;
    background-color: rgb(99, 255, 8);
    border-radius: 10px;
    color:blue;
    height: 40px;
    width: 40%;
    text-align: center;
    font-size: 22px;
    font-weight: 900;
}
tr{
    height: 50px;
}
.tableData thead tr{
    background-color: rgb(229, 255, 31);
    color: rgb(43, 16, 244);
}
tr{
    border-radius: 20px;
}