.kathaHistoryBased{
    /* border: 2px solid red; */
    height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 20px;
}
.kathaHistoryBased button{
    height: 40px;
    width: 300px;
    margin-left: 60px;
    border-radius: 20px;
    color:blue;
    font-size: 20px;
    font-weight: 900;
    cursor: pointer;
}
.kathaHistoryBased button:nth-child(8){
    background-color: red;
    color:white;
}
tr:nth-child(odd){
    background-color: rgb(40, 7, 53);
    color: rgb(255, 255, 255);
}
thead tr{
   background-color: red;
   color: white;
}
.tableContainer{
    border: 2px solid white;
    overflow: scroll;
}