.mainContainer{
    /* border: 2px solid red; */
    height: 570px;
    position: absolute;
    width: 100%;
    bottom: 65px;
    color:white;
   
}
.subContainer{
    display: flex;
    box-shadow: 10px 0px 10px 0px white;
    height: 550px;
    margin-top: 10px;
    border-radius: 20px;
    width: 100%;
    background-color: rgb(255, 255, 255);
}
.tableContainer{
    height: 500px;
    width: 70%;
    /* border: 2px solid red; */
    margin-top: 20px;
    margin-left: 10px;
    border-radius: 20px;
    background-color: rgb(247, 151, 7);
    box-shadow: 0px 0px 5px 0px white;
}
.tableData{
    width: 80%;
    /* border: 2px solid orange; */
    margin-left: 10%;
    margin-top: 20px;
    scroll-behavior: auto;
    text-align: center;
    font-size: 20px;
    font-weight: 900;
    background-color: rgb(159, 239, 255);
    color: black;
    border-radius: 20px;
}
.formContainer{
    /* border: 2px solid rgb(9, 243, 9); */
    height: 500px;
    margin-top: 20px;
    border-radius: 20px;
    width: 25%;
    margin-left: 30px;
    background-color: rgb(49, 52, 235);
    box-shadow: 0px 0px 10px 0px white;
    
}
.formdata{
    border: 2px solid rgb(194, 82, 82);
    height: 450px;
    margin-top: 20px;
    width: 90%;
    margin-left: 20px;
    border-radius: 20px;
    background-color: rgb(31, 255, 23);
    box-shadow: 0px 0px 10px 0px white;
   

}
.formdata div{
    padding-left: 90px;
    height: 100px;
    padding-top: 20px;
    font-size: 20px;
    font-weight: 600;
    color: blue;
}
.formdata div input{
    height: 40px;
    width: 200px;
    border-radius: 20px;
    font-size: 20px;
    font-weight: 800;
    color: red;
    padding-left: 0px;
    
}
.formdata div button{
    height: 40px;
    width: 150px;
    border-radius: 20px;
    background-color: blue;
    color: white;
    font-size: 20px;
    font-weight: 900;
    margin-left: 30px;
    
}
span{
   position: absolute;
   top: -15px;
   font-size: 20px;
   font-weight: 800;
   left: 20px;
   letter-spacing: 2px;
   word-spacing: 5px;
   color: rgb(77, 254, 22);
}
.selectionField{
    height: 40px;
    width: 250px;
    border-radius: 20px;
    font-size: 20px;
    padding-left: 10px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
}
.selectionField option{
    font-size: 16px;
    
}